import React, { Fragment, useState, useEffect } from "react";
import CryptoJS from "crypto-js";
import {
  Cell,
  Counter,
  Div,
  MiniInfoCell,
  Button,
  Input,
  FormLayout,
  Snackbar,
} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";

const Delivery = ({
  deliv,
  sum,
  activePVZ,
  go,
  typeDelivery,
  fetchedUser,
  order,
  who,
}) => {
  const [street, setStreet] = useState("");
  const [house, setHouse] = useState("");
  const [room, setRoom] = useState("");
  const [phone, setPhone] = useState("");
  const [snackbar, setSnackbar] = useState(null);
  const [signature, setSignature] = useState("");
  const [payLink, setPayLink] = useState('');
  const [date, setDate] = useState("");
  useEffect(async() => {
    const date = Date.now();
    setDate(date);
    const data = {
      testing: "1",
      salt: "dPUTLtbMfcTGzkaBnGtseKlcQymCLrYI",
      order_id: String(Math.round(date / 1000)),
      custom_order_id: String(Math.round(date / 1000)),
      amount: "1",
      merchant: "57170613-71d8-444e-8b69-8253749eee13",
      description: `Заказ №${String(Math.round(date / 1000))}`,
      success_url: "https://pay.modulbank.ru/success",
      unix_timestamp: String(Math.round(date / 1000)),
    };
    await fetch("https://sahpossum.herokuapp.com/pay/sign", {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      mode: "cors", // no-cors, cors, *same-origin
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "same-origin", // include, *same-origin, omit
      headers: {
        "Content-Type": "application/json",
      },
      redirect: "follow", // manual, *follow, error
      referrer: "no-referrer", // no-referrer, *client
      body: JSON.stringify(data), // тип данны
    })
        .then((res) => res.json())
        .then((res) => {
          const paylink = encodeURI(`https://pay.modulbank.ru/pay?testing=1&salt=dPUTLtbMfcTGzkaBnGtseKlcQymCLrYI&order_id=${String(Math.round(date / 1000))}&custom_order_id=${String(Math.round(date / 1000))}&merchant=57170613-71d8-444e-8b69-8253749eee13&description=Заказ №${String(Math.round(date / 1000))}&success_url=https://pay.modulbank.ru/success&unix_timestamp=${Math.round(Date.now() / 1000)}&amount=1&signature=${res}`);
          setPayLink(paylink)
        });
  }, []);
  const pay = () => {
    try {
      if (who === "sdek") {
        if (!activePVZ) throw "Не выбран пункт доставки";
        if (activePVZ.name === "Доставить домой") {
          if (street === "") throw "Не указан адрес доставки";
          if (house === "") throw "Не указан адрес доставки";
          if (room === "") throw "Не указан адрес доставки";
        }
      }
      if (who === "PR") {
        if (street === "") throw "Не указан адрес доставки";
        if (house === "") throw "Не указан адрес доставки";
        if (room === "") throw "Не указан адрес доставки";
      }
      setSnackbar(
        <Snackbar layout="vertical" onClose={() => setSnackbar(null)}>
          <pre>
            Заказ:
            {JSON.stringify(order.meta, true, 2)}
            Доставить:{`${who}  ${street} ${house} ${room}`}
          </pre>
        </Snackbar>
      );
      // if (fetchedUser.id){
      //     bridge.send("VKWebAppOpenPayForm", {"app_id": 7600142, "action": "pay-to-user", "params": {
      //             "amount": order.meta.sum +Math.round(Number(deliv.price)),
      //             "user_id": 199500866,
      //             "description": `Оплата товаров на сумму ${order.meta.sum +Math.round(Number(deliv.price))} в магазине "Сахарный поссум"`
      //         },
      //     });
      // }
    } catch (e) {
      setSnackbar(
        <Snackbar layout="vertical" onClose={() => setSnackbar(null)}>
          {e}
        </Snackbar>
      );
    }
  };

  if (deliv.error) {
    return (
      <>
        <MiniInfoCell textWrap={"full"}>{deliv.error}</MiniInfoCell>
      </>
    );
  }

  return (
    <Fragment>
      <MiniInfoCell
        after={<Counter>{Math.round(Number(deliv.price))} руб.</Counter>}
      >
        Стоимость доставки
      </MiniInfoCell>
      <MiniInfoCell after={deliv.deliveryDateMax}>Доставка до:</MiniInfoCell>
      <MiniInfoCell after={Math.round(Number(deliv.price)) + Number(sum)}>
        Итого к оплате:{" "}
      </MiniInfoCell>
      {typeDelivery === "sdek" && (
        <Cell
          expandable
          indicator={activePVZ !== null ? activePVZ.name : "Не выбрано"}
          onClick={() => go("GetPvzList")}
        >
          Пункт выдачи
        </Cell>
      )}
      {who === "sdek" && (
        <Cell
          expandable
          onClick={() => go("GetPvzList")}
          indicator={activePVZ !== null ? activePVZ.name : "Не выбрано"}
        >
          Место доставки
        </Cell>
      )}
      {who === "sdek" &&
        activePVZ !== null &&
        activePVZ.name === "Доставить домой" && (
          <FormLayout>
            <Input
              value={street}
              onChange={(e) => setStreet(e.target.value)}
              top={"Улица"}
            />
            <Input
              value={house}
              onChange={(e) => setHouse(e.target.value)}
              top={"Дом"}
            />
            <Input
              value={room}
              onChange={(e) => setRoom(e.target.value)}
              top={"Квартира"}
            />
            <Input
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              top={"Телефон"}
            />
          </FormLayout>
        )}
      {who === "PR" && (
        <FormLayout>
          <Input
            value={street}
            onChange={(e) => setStreet(e.target.value)}
            top={"Улица"}
          />
          <Input
            value={house}
            onChange={(e) => setHouse(e.target.value)}
            top={"Дом"}
          />
          <Input
            value={room}
            onChange={(e) => setRoom(e.target.value)}
            top={"Квартира"}
          />
          <Input
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            top={"Телефон"}
          />
        </FormLayout>
      )}
      <Div>
        <a href={payLink}>
          <Button
              mode={"outline"}
              size={"xl"}
          >
            Оплатить
          </Button>
        </a>
      </Div>
    </Fragment>
  );
};
export default Delivery;
